

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", serif;
  background-color: #868686;

}

.header {
  color: #fff;
  text-shadow: 5px 5px 5px red;
  text-align: center;
  background-color: blue;
  height: 5rem;
  line-height: 5rem;
  text-transform: capitalize;
  letter-spacing: 2px;
}

.card_img {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;

}

.card {
  width: 300px;
  height: auto;
  float: left;
  flex-direction: row;
  margin: 6%;
  /* border-top-left-radius: 15px; */
  border-radius: 15px;
  /* border-top-right-radius: 15px; */
  align-items: center;
  transition: all 0.4s cubic-bezier(0, 0, 0, 0.1);
  align-items: center;
  background-color: #fff;
  
}

.card:hover{
  transform: scale(1.05, 1.05);
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
}

.cards {
  width: 100%;
  height: auto;

}

.card_info {
  text-align: center;
  background-color: #fff;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

.card_category {

  color: #868686;
  margin-top: 50px;
  letter-spacing: 1px;
  width: 500;
  font-weight: bold;
  background-color: #fff;
}

.card_title {
  margin-top: 10px;
  letter-spacing: 2px;
  width: 500;
  font-weight: bold;
  background-color: #fff;
}

button {
  padding: 10px 30px;
  border-radius: 10px;
  border: none;
  outline: none;
  margin-bottom: 10px;
  margin-top: 10px;
  color: #868686;
  letter-spacing: 2px;
  color: #fff;
  text-transform: capitalize;
  cursor: pointer;
  font-size: 15px;

}

button:hover {
  background-color: white;
  color: blueviolet;
  border: 1px solid #868686;
}


@media (max-width: 768px) {
  .card {
    float: none;
    margin: 5%;
    width: 90%;
  }

  .card_img {
    width: 100%;

  }
}

@media (max-width: 620px) {
  .header {
    font-size: 20px;

  }
}